/*Обнуление*/
*{padding: 0;margin: 0;border: 0;}
*,*:before,*:after{-moz-box-sizing: border-box;-webkit-box-sizing: border-box;box-sizing: border-box;}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}
nav,footer,header,aside{display: block;}
html,body{height:100%;width:100%;font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%;}
input,button,textarea{font-family:inherit;}
input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner{padding:0;border:0;}
a,a:visited{text-decoration: none;}
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}
h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight: inherit;}
/*--------------------*/

$montserrat: 'Montserrat', sans-serif;
$onefz: 16px;

.container{
    width: 1140px;
    padding: 0 15px;
    margin: 0 auto;
}
@media screen and (max-width: 1150px){
    .container{
        width: 100%;
    }
}
.post-image{
    border-radius: 5px;
}
header{
    .top-line{
        height: 40px;
        background-color: #191919;
        display: flex;
        align-items: center;
        .line-container{
            display: flex;
            justify-content: space-between;
        }
        .top-icon{
            display: flex;
            align-items: center;
            .icons-item{
                padding: 0 8px;
                font-size: 16px;
                a{
                    color: #707070;
                }
            }
        }
        @media screen and ( max-width: 540px ){
            .top-icon{
                display: none;
            }
        }
        .language{
            margin-left: auto;
            display: flex;
            align-items: center;
            svg{
                fill: #707070;
            }
            a{
                font-family: $montserrat;
                font-size: 16px;
                color: #707070;
                font-weight: 400;
                text-transform: uppercase;
                padding: 0 8px;
            }
            .user{
                cursor: pointer;
                display: flex;
                align-items: center;
            }
            .vertical-line{
                height: 20px;
                width: 1px;
                background-color: #707070;
                margin: 0 8px;
            }
            .lang{
                >svg{
                    margin: 0 8px;
                }
                cursor: pointer;
                align-items: center;
                display: flex;
                .arrow svg{
                    margin-right: 8px; 
                    transition: 0.2s;
                }
                .active-arrow{
                    transition: 0.2s;
                    transform: rotate(-90deg);
                }
                .language-grop{
                    float: left;
                    margin-top: 0;
                }
                .visibility{
                    display: block;
                }
                .toggle-ul{
                    z-index: 999;
                    margin-top: 78px;
                }
                ul{
                    background-color: #191919;
                    ul::after{
                        content: "";
                        display: block;
                        clear: both;
                    }
                }
                li{
                    display: none;
                    a{
                        text-align: center;
                        display: inline-block;
                        padding: 5px 3px;
                    }
                }
            }
        }
    }
    @media screen and ( max-width: 540px ){
        // .top-line{
        //     background: #303f9f;
        // }
    }
    .header-menu-line{
        z-index: 999;
        background-color: #ccc;
        height: 0;
        @media screen and ( max-width: 1140px ){
            .container{
                width: 100%;
            }
        }
        .container-menu{
            position: relative;
            z-index: 99;
            display: flex;
            width: 100%;
            padding-top: 25px;
            .right-icon{
                z-index: 80;
                margin-top: -5px;
                .icon-container{
                    display: flex;
                    align-items: center;
                    svg{
                        fill: #fff;
                    }
                }
                .vertical-line{
                    z-index: 89;
                    margin: 0 15px;
                    background-color: #5b5b5b;
                    width: 1px;
                    height: 30px;
                }
            } 
            @media screen and ( max-width: 530px ){
                .icon-container{
                    a{
                        display: none;
                        &:last-child{
                            display: block;
                            margin-left: auto;
                        }
                    }
                    .vertical-line{
                        display: none;
                    }

                }
                .right-icon{
                    width: 134px;
                }
            }
            nav{
                z-index: 80;
                margin: 0 auto;
                .header-menu-drop:hover .active{
                    transition: 0.3s;
                    border: none;
                }
                .header-menu-drop>li{
                    transition: 0.2s;
                    padding: 0 15px;
                    padding-bottom: 20px;
                    &:hover>ul{
                        transition: 0.5s;
                        display: block;
                        border: none;
                    }
                    .one-floor{
                        font-family: $montserrat;
                        font-size: 14px;
                        font-weight: bold;
                        text-transform: uppercase;
                        color: #fff;
                        transition: 0.5s;
                    }
                    .one-floor::after{
                        content: url(../img/svg/keyboard_down.svg);
                        float: right;
                        margin-top: -5px;
                    }
                    .drop-ul{
                        transition: 0.5s;
                        li{
                            margin: 5px 0;
                            transition: 0.5s;
                            a{
                                font-family: $montserrat;
                                text-align: center;
                                text-transform: uppercase;
                                color: #fff;
                                transition: 0.5s;
                            }
                        }
                    }
                }
                .header-menu-svg{
                    margin-top: 15px;
                }
            }
            .header-menu-drop{
                display: flex;
                .active{
                    border-bottom: 4px solid #ff4081;
                }
                .two-floor{
                    transition: 0.5s;
                    display: none;
                }
            }
        }
        .logo{
            text-align: center;
            z-index: 9999;
            text-transform: uppercase;
            font-family: $montserrat;
            font-size: 24px;
            font-weight: bold;
            color: #fff;
        }
        @media screen and ( max-width: 1230px ){
            .logo{
                margin: 0 auto;
                text-align: center;
            }
            .text-logo{
                width: 100px;
                text-align: center;
                margin: 0 auto;
            }
        }
        .header-burger{
            float: left;
            cursor: pointer;
            z-index: 5;
            display: none;
            width: 30px;
            height: 20px;
            position: relative;
            &::after, &::before{
                content: '';
                position: absolute;
                background-color: #fff;
                width: 100%;
                height: 2px;   
                transition: all 0.3s ease 0s;         
            }
            span{
                background-color: #fff;
                width: 100%;
                height: 2px; 
                position: absolute;
                top: 9px;
            }
            &::before{
                top: 0;
            }
            &::after{
                bottom: 0;
            }
        }
        .active-burger::before{
            transform: rotate(45deg);
            top: 9px;
        }
        .active-burger::after{
            transform: rotate(-45deg);
            bottom: 9px;
        }
        .active-burger span{
            display: none;
        }
        .container-burger{
            width: 134px;
            display: none;
        }
        @media screen and ( max-width: 1230px ){
            .header-burger{
                z-index: 9999;
                display: flex;
                cursor: pointer;
            }
            .container-burger{
                display: block;
            }
        }
    }
    @media screen and ( max-width: 540px ){
        .header-menu-line{
            height: 70px;
            background: #3f51b5;
        }
    }
    #slider{
        width: 100%;
        transition: 0.2s;
        overflow: hidden;
        .slick-list {
            overflow: hidden;
        }
        .slick-arrow{
            position: absolute;
            top: 50%;
            z-index: 1;
            font-size: 0;
        }
        .slick-prev{
            position: absolute;
            left: 30px;
            background: url(../img/svg/keyboard_arrow_left-24px.svg) 0 0 no-repeat;
            width: 24px;
            height: 24px;
        }
        .slick-next{
            position: absolute;
            right: 30px;
            background: url(../img/svg/keyboard_arrow_right-24px.svg) 0 0 no-repeat; 
            width: 24px;
            height: 24px;
        }
        .arrow{
            position: absolute;
            top: 50%;
            a{
                fill: #fff;
                font-size: 50px;
            }
        }
        .right-arrow{
            right: 50px;
        }
        .left-arrow{
            left: 50px;
        }
        .slider-container{
            position: relative;
            display: flex;
            min-width: 0;
            height: 800px;
            .slick-track{
                display: flex;
                min-width: 0;
            }
        } 
        @media screen and ( max-width: 540px ){
            .slider-container{
                visibility: hidden;
                transform: translate(-100%); 
                position: absolute;
            }
        }
        .slide-item{
            display: flex;
            width: 100%;
            height: 800px;
            justify-content: center;
            align-items: center;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
            .content{
                align-items: center;
                margin: auto auto;
                width: 900px; 
                height: 680px;
            }
            .header-information{
                padding-top: 25%;
                text-align: center;
                p{
                    font-family: $montserrat;
                    color: #fff;
                    font-size: 28px;
                    text-transform: uppercase;
                }
                h1{
                    padding: 10px 0;
                    font-family: $montserrat;
                    color: #fff;
                    font-size: 42px;
                    text-transform: uppercase;
                    font-weight: 900 ;
                    width: 850px;
                    margin: 0 auto;
                }
                @media screen and (max-width: 840px){
                    p{
                        font-size: 20px;
                    }
                    h1{
                        font-size: 30px;
                        width: 500px;
                    }
                }
                .button{
                    margin-top: 30px;
                    a{  
                        text-transform: uppercase;
                        font-family: $montserrat;
                        color: #fff;
                        padding: 15px 30px;
                        border-radius: 30px;
                        background: #303f9f;
                        &:hover{
                            box-shadow: 0px 17px 24px 0px rgba(48, 63, 159, 0.2);
                        }    
                    }
                }
            }
            .staff-cart{
                padding-top: 95px;
                text-align: center;
                .staff-photo{
                    height: 58px;
                    width: 58px;
                    margin: 0 auto;
                    img{
                        border-radius: 50%;
                    }
                }
                .staff-name p{
                    padding-top: 15px;
                    font-family: $montserrat;
                    color: #fff;
                    font-size: 14px;
                    font-weight: bold;
                }
                .data{
                    font-family: $montserrat;
                    color: #fff;
                    font-size: 12px;
                    opacity: 54%;
                    margin-top: 15px;
                }
                .staff-comments{
                    width: 170px;
                    font-family: $montserrat;
                    color: #fff;
                    font-size: 12px;
                    opacity: 54%;
                    margin: 30px auto;
                    .number-comments{
                        display: flex;
                        margin: 0 auto;
                    }
                }
            }
        }
    }
    @media screen and ( max-width: 1230px ) {
        nav{
            display: none;
        }
        .container-menu{
            justify-content: space-between;
        }
    }
    .header-sm-menu{
        transition: all 0.3s ease 0s;
        position: absolute;
        top: 0;
        left: 0;
        height: 100vh;
        background: #191919;
        z-index: 9;
        display: none;
        transform: translate(-100%);    
    }
    .active-burger{
        transform: translate(0);
    }
    @media screen and ( max-width: 1230px ){
        .header-sm-menu{
            display: block;
            padding-top: 150px;
            nav{
                width: 338px;
                z-index: 80;
                margin: 0 auto;
                display: block;
                .header-menu-drop>li{
                    border: none;
                    transition: 0.2s;
                    padding: 0 15px;
                    padding-bottom: 20px;
                    text-align: center;
                    &:hover>ul{
                        transition: 0.5s;
                        display: block;
                    }
                    .one-floor{
                        font-family: $montserrat;
                        font-size: 14px;
                        font-weight: bold;
                        text-transform: uppercase;
                        color: #fff;
                        transition: 0.5s;
                    }
                    .one-floor::after{
                        content: url(../img/svg/keyboard_down.svg);
                        margin-top: 5px;
                    }
                    .drop-ul{
                        transition: 0.5s;
                        li{
                            margin: 5px 0;
                            padding-right: 20px;
                            transition: 0.5s;
                            a{
                                font-family: $montserrat;
                                text-align: center;
                                text-transform: uppercase;
                                color: #fff;
                                transition: 0.5s;
                            }
                        }
                    }
                }
                .header-menu-svg{
                    margin-top: 15px;
                }
            }
            .header-menu-drop{
                .two-floor{
                    transition: 0.5s;
                    display: none;
                }
            }
        }
    }
}
main{
    background-color: #f5f5f5 ;
    ul{
        display: flex;
        li{
            margin: 0 7px;
            background-color: #f5f5f5;
            border-radius: 30px;
            padding: 10px 0;
            &:hover{
                background-color: #fff;
            }
        }
        a{
            font-weight: 400;
            color: #000;
            font-size: 16px;
            font-family: $montserrat;
            text-transform: uppercase;
            padding: 10px 30px ;
        }
    }
    .kategory{
        margin-bottom: 30px;
        background-color: #fff;
        width: 100%;
        height: 60px;
        border-bottom: 1px #dddddd solid;
        .container{
            height: 100%;
        }
        .content-kategory{
            display: flex;
            align-items: center;
            height: 100%;
            .kategory-li{
                background: #fff;
                border-radius: 30px;
                a{
                    border-radius: 30px;
                    background: #fff;
                    color: #3f51b5;
                    border: 1px solid #3f51b5;
                }
            }
            p{
                font-family: $montserrat;
                font-size: 16px;
                text-transform: uppercase;
                font-weight: bold;
            }
        }
        @media screen and ( max-width: 850px ){
            .content-kategory{
                display: none;
            }
        }
    } 
    .kategory-sm{
        display: none;
    }
    .select{
        display: block;
        width: 100%;
        position: relative;
        appearance: none;
        background-color: #fff;
        border: 1px solid #e0e0e0;
        outline: none;
        border-radius: 5px;
        padding: 12px 15px;
        font-family: 'Open Sans', sans-serif;
        font-size: 16px;
    }
    .select-container{
        margin: 0 auto;
        width: 100%;
        position: relative;
        border-radius: 5px;
        overflow: hidden;
        .arrow-block{
            background-color: #eeeeee;
            height: 100%;
            position: absolute;
            width: 44px;
            right: 0;
            top: 0;
            border-left: 1px solid #e0e0e0;
        }
    }
    .select-container::before{
        position: absolute;
        z-index: 5;
        top: 55%;
        content: "";
        right: 15px;
        display: block;
        width: 10px;
        height: 5px;
        border-style: solid;
        border-width: 5px 5px 0 5px;
        border-color: #6d6d6d transparent transparent transparent;
    }
    .select-container::after{
        position: absolute;
        bottom: 55%;
        content: "";
        width: 10px;
        height: 5px;
        right: 15px;
        border-style: solid;
        border-width: 0 5px 5px 5px;
        border-color: transparent transparent #6d6d6d transparent;
    }
    @media screen and ( max-width: 850px ){
        .kategory-sm{
            height: 100%;
            display: flex;
            align-items: center;
        }
    }
    .row{
        display: flex;
        overflow: hidden;
        article{
            border-radius: 5px;
            margin-bottom: 20px;
            background: #fff;
            .post-image{
                transition: 0.5s;
                display: flex;
                cursor: pointer;
                background-size: cover;
                background-position: center;
                position: relative;
                justify-content: center;
                align-items: center;
                &:hover .hover-icon{
                    display: flex;
                    transition: 0.5s;
                }
                .hover-icon{
                    transition: 0.5s;
                    width: 50px;
                    height: 50px;
                    display: none;
                    justify-content: center;
                    align-items: center;
                    color: #fff;
                    font-size: 50px;
                }
                .likes-and-comments{
                    position: absolute;
                    display: flex;
                    right: 20px;
                    bottom: 20px;
                    color: #fff;
                    .comments{
                        display: flex;
                    }
                    .likes{
                        display: flex;
                        padding-left: 20px;
                    }
                    p{
                        padding-left: 5px;
                        font-family: $montserrat;
                        color: #fff;
                    }
                }
            }
            .post-content{
                min-height: 0;
                .post-h{
                    padding: 20px;
                    h2{
                        font-family: $montserrat;
                        font-size: 18px;
                        font-weight: bold;
                        padding-bottom: 15px;
                    }
                    p{
                        font-family: 'Open Sans';
                        font-size: 16px;
                        line-height: 1.5em;
                    }
                }
            }
            .post-autor{
                padding: 20px;
                border-top: 1px solid #ebeae9;
                display: flex;
                align-items: center;
                .name{
                    padding-left: 10px;
                    p{
                        font-family: $montserrat;
                        font-size: 14px;
                        font-weight: bold;
                    }
                    .data{
                        p{
                            padding-top: 10px;
                            color: #a8a8a8;
                            font-weight: normal;
                        }
                    }

                }
                .autor-img img{
                    cursor: pointer;
                    border-radius: 50%;
                }
                .setting-post{
                    cursor: pointer;
                    margin-left: auto;
                    margin-right: 10px;
                    .icon-ellipse{
                        margin: 3px;
                        height: 5px;
                        width: 5px;
                        border-radius: 50%;
                        background-color: #000;
                    }
                }
            }
        }
        .block-posts{
            flex: 0 0 100%;
            .posts-container{
                max-width: 1170px;
                padding: 0 15px;
                margin: 0 auto;
                .post-body{
                    width: 74%;
                    padding-right: 40px;                   
                }
                @media screen and ( max-width: 1230px ){
                    .post-body{
                        width: 100%;
                        padding-right: 0px;                   
                    }
                }
                .container-main-post{
                    margin-bottom: 20px;
                    .main-post{
                        .post-image{
                            min-height: 420px;
                        }
                    }
                }
                .central-post{
                    margin-bottom: 20px;
                    article{
                        display: flex;
                        .post-image{
                            width: 50%;
                        }
                        @media screen and ( max-width: 750px ){
                            .post-image{
                                width: 90%;
                            }
                        }
                        .post-h{
                            padding: 10px;
                            h2{
                                font-size: 16px;
                            }
                            p{
                                position: relative;
                                font-size: 14px;
                                overflow: hidden;
                                position: relative; 
                                max-height: 100px;
                            }
                            p::after{
                                content: '';
                                position: absolute;
                                right: 0;
                                height: 20px;
                                bottom: 3px;
                                width: 60px;
                            }
                        }
                        .post-autor{
                            padding: 10px;
                        }
                    }
                }
                @media screen and ( max-width: 450px ){
                    .central-post{
                        .post-h{
                            p{
                                display: none;
                            }
                        }
                    }
                }
                .horisontal-post{
                    display: grid;
                    grid-template-columns: 1fr 1fr 1fr;
                    grid-column-gap: 10px;
                    min-width: 0;
                    .slider-block{
                        display: block;
                        max-width: 100%;
                        position: relative;
                        height: 165px;
                    }
                    .slider-container{
                        min-width: 0;
                        position: absolute;
                        width: 100%;
                        top: 0;
                        left: 0;
                    }
                    .post-image{
                        width: 100%;
                    }
                    .slick-arrow{
                        position: absolute;
                        top: 50%;
                        z-index: 1;
                        font-size: 0;
                    }
                    .slick-prev{
                        left: 20px;
                        background: url(../img/svg/keyboard_arrow_left-24px.svg) 0 0 no-repeat;
                        width: 24px;
                        height: 24px;
                    }
                    .slick-next{
                        right: 20px;
                        background: url(../img/svg/keyboard_arrow_right-24px.svg) 0 0 no-repeat; 
                        width: 24px;
                        height: 24px;
                    }
                    .slick-list{
                        width: 100%;
                        overflow: hidden;
                    }
                    .slick-track{
                        display: flex;
                        min-width: 0;
                    }
                    article{
                        .post-image{
                            height: 165px;
                        }
                    }
                    .post-content{
                        .post-h{
                            p{
                                overflow: hidden;
                                position: relative; 
                                height: 100px;
                                &::after{
                                    content: ''; /* Выводим элемент */
                                    position: absolute; /* Абсолютное позиционирование */
                                    right: 0;  /* Положение элемента */
                                    height: 20px;
                                    bottom: 3px;
                                    width: 60px; /* Ширина градиента*/
                                    background: linear-gradient(to right, rgba(255, 255, 255, 0.5), #fff 100%);
                                }
                            }
                            @media screen and ( max-width: 750px ){
                                p.post-text{
                                    display: none;
                                }
                            }
                        }
                        @media screen and ( max-width: 550px ){
                            .post-h{
                                display: none;
                            }
                        }
                    }
                    @media screen and ( max-width: 550px ){
                        .post-autor{
                            display: none;
                        }
                    }
                }
                @media screen and ( max-width: 500px ){
                    .horisontal-post{
                        grid-template-columns: 1fr 1fr ;
                    }
                }
                @media screen and ( max-width: 750px ){
                    .autor-img{
                        display: none;
                    }
                }
                .buttom-post{
                    margin-bottom: 20px;
                    display: grid;
                    grid-template-columns: 1fr 1fr; 
                    grid-column-gap: 10px;
                    article{
                        min-height: 620px; 
                        .post-content{
                            p{
                                overflow: hidden;
                                position: relative; 
                                height: 100px;
                                &::after{
                                    content: ''; /* Выводим элемент */
                                    position: absolute; /* Абсолютное позиционирование */
                                    right: 0;  /* Положение элемента */
                                    height: 20px;
                                    bottom: 3px;
                                    width: 60px; /* Ширина градиента*/
                                    background: linear-gradient(to right, rgba(255, 255, 255, 0.5), #fff 100%);
                                }
                            }
                        }
                    }
                    .post-image{
                        height: 50%;
                    }
                }
                @media screen and (max-width: 450px) {
                    .buttom-post{
                        display: block;
                        .post-image{
                            height: 300px;
                        }
                    }
                }
                .more{
                    display: flex;
                    justify-content: flex-end;
